import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { getAllReferral } from '../../store/actions/ReferralActions';
import MainCard from '../../ui-component/cards/MainCard';
import { withStyles, useTheme } from '@material-ui/styles';
import {
    Grid,
    Typography,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    Paper,
    TablePagination,
    OutlinedInput,
    InputAdornment
} from '@material-ui/core';

import { IconSearch } from '@tabler/icons';
import SubjectIcon from '@material-ui/icons/Subject';
import useStyles from '../ViewTableStyles';
import useUserRole from '../../hooks/useUserRole';

const StyledTableRow = withStyles((theme) => ({
    root: {
        '&:nth-of-type(even)': {
            backgroundColor: theme.palette.action.hover
        }
    }
}))(TableRow);

export default function Referrals() {
    const classes = useStyles();
    const theme = useTheme();
    useUserRole({ roleList: ['ADMIN', 'SUPER_ADMIN'] });

    const dispatch = useDispatch();
    const [value, setValue] = useState('');

    const [page, setPage] = useState(0);
    const [rowsPerPage, setRowsPerPage] = useState(25);

    const { allReferrals } = useSelector((state) => state?.referral);
    const newData = allReferrals;

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
    };

    useEffect(() => {
        dispatch(getAllReferral(value ? value : '', page + 1, rowsPerPage));
    }, [value, page, rowsPerPage]);

    return (
        <>
            <MainCard>
                <div className={classes.titleContainer}>
                    <SubjectIcon className={classes.viewIcon} />
                    <span className={classes.pageTitle}>&nbsp; All Referrals </span>
                </div>
                <Paper elevation={5} className={classes.root}>
                    <Grid style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <div>
                            <OutlinedInput
                                label="hello"
                                className={classes.searchControl}
                                id="input-search-header"
                                value={value}
                                onChange={(e) => setValue(e?.target?.value)}
                                placeholder="Search"
                                startAdornment={
                                    <InputAdornment position="start">
                                        <IconSearch stroke={1.5} size="1rem" className={classes.startAdornment} />
                                    </InputAdornment>
                                }
                            />
                        </div>
                    </Grid>
                    <TableContainer className={classes.tableContainer}>
                        <Table className={classes.table} aria-label="simple table">
                            <TableHead className={classes.tableHeaderCell}>
                                <StyledTableRow>
                                    <TableCell> Name </TableCell>
                                    <TableCell> Email </TableCell>
                                    <TableCell> Phone </TableCell>
                                    <TableCell> Join </TableCell>
                                </StyledTableRow>
                            </TableHead>
                            {!newData ? (
                                <caption style={{ textAlign: 'center' }}> Sorry! No data available </caption>
                            ) : (
                                <TableBody>
                                    {newData?.content?.map((row, index) => (
                                        <StyledTableRow key={index}>
                                            <TableCell> {row?.name} </TableCell>
                                            <TableCell> {row?.email} </TableCell>
                                            <TableCell> {row?.mobile} </TableCell>

                                            <TableCell>
                                                <Typography
                                                    className={classes.status}
                                                    style={{
                                                        backgroundColor:
                                                            (row?.joined === 'true' && theme.palette.success.main) ||
                                                            (row?.joined === 'false' && theme.palette.warning.dark)
                                                    }}
                                                >
                                                    {`${row?.joined}`}
                                                </Typography>
                                            </TableCell>
                                        </StyledTableRow>
                                    ))}
                                </TableBody>
                            )}
                        </Table>
                    </TableContainer>
                    <TablePagination
                        rowsPerPageOptions={[25, 50, 100]}
                        component="div"
                        count={newData?.totalElements || 0}
                        rowsPerPage={rowsPerPage}
                        page={page}
                        onPageChange={handleChangePage}
                        onRowsPerPageChange={handleChangeRowsPerPage}
                        sx={{
                            '.MuiTablePagination-displayedRows': {
                                marginBottom: 0
                            },
                            '.MuiTablePagination-selectLabel': {
                                marginBottom: 0
                            }
                        }}
                    />
                </Paper>
            </MainCard>
        </>
    );
}
