import { GET_ALL_MODULE_DETAIL_START, GET_ALL_MODULE_DETAIL_SUCCESS, GET_ALL_MODULE_DETAIL_ERROR } from '../../constants';

const initialState = {
    loading: false,
    allDetails: null,
    getDetailsData: null,
    message: ''
};

export default function GetAllModuleDetailReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_MODULE_DETAIL_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_MODULE_DETAIL_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_MODULE_DETAIL_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
