import {
    GET_ALL_MEETING_START,
    GET_ALL_MEETING_SUCCESS,
    GET_ALL_MEETING_ERROR,
    GET_MEETING_START,
    GET_MEETING_SUCCESS,
    GET_MEETING_ERROR
} from '../../constants';

const initialState = {
    loading: false,
    allMeetings: null,
    getMeetingData: null,
    message: ''
};

export default function GetAllMeetingReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_MEETING_START:
        case GET_MEETING_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_MEETING_SUCCESS:
        case GET_MEETING_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_MEETING_ERROR:
        case GET_MEETING_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
