import {
    GET_ALL_DISCOUNT_START,
    GET_ALL_DISCOUNT_SUCCESS,
    GET_ALL_DISCOUNT_ERROR,
    GET_DISCOUNT_START,
    GET_DISCOUNT_SUCCESS,
    GET_DISCOUNT_ERROR
} from '../../constants';

const initialState = {
    loading: false,
    allDiscounts: null,
    getDiscountData: null,
    message: ''
};

export default function GetAllDiscountReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_DISCOUNT_START:
        case GET_DISCOUNT_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_DISCOUNT_SUCCESS:
        case GET_DISCOUNT_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_DISCOUNT_ERROR:
        case GET_DISCOUNT_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
