import { GET_ALL_TRANSACTION_START, GET_ALL_TRANSACTION_SUCCESS, GET_ALL_TRANSACTION_ERROR } from '../../constants';

const initialState = {
    loading: false,
    getAllTransactionData: null,
    getAllModuleTransactionData: null,
    message: ''
};

export default function GetAllTransactionReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_TRANSACTION_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_TRANSACTION_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_TRANSACTION_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
