import { GET_ALL_LANGUAGE_START, GET_ALL_LANGUAGE_SUCCESS, GET_ALL_LANGUAGE_ERROR } from '../../constants';

const initialState = {
    loading: false,
    allLanguages: null,
    languagesList: null,
    message: ''
};

export default function GetAllLanguageReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_LANGUAGE_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_LANGUAGE_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_LANGUAGE_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
