import {
    GET_ALL_ASSIGNMENT_SUBMISSION_START,
    GET_ALL_ASSIGNMENT_SUBMISSION_SUCCESS,
    GET_ALL_ASSIGNMENT_SUBMISSION_ERROR,
    GET_ASSIGNMENT_SUBMISSION_START,
    GET_ASSIGNMENT_SUBMISSION_SUCCESS,
    GET_ASSIGNMENT_SUBMISSION_ERROR
} from '../../constants';

const initialState = {
    loading: false,
    allAssignment_Submissions: null,
    getAssignment_Submission_Data: null,
    message: ''
};

export default function GetAllAssignment_Submission_Reducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_ASSIGNMENT_SUBMISSION_START:
        case GET_ASSIGNMENT_SUBMISSION_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_ASSIGNMENT_SUBMISSION_SUCCESS:
        case GET_ASSIGNMENT_SUBMISSION_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_ASSIGNMENT_SUBMISSION_ERROR:
        case GET_ASSIGNMENT_SUBMISSION_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
