import { GET_ALL_STUDENT_MODULE_START, GET_ALL_STUDENT_MODULE_SUCCESS, GET_ALL_STUDENT_MODULE_ERROR } from '../../constants';

const initialState = {
    loading: false,
    allStudentModules: null,
    message: ''
};

export default function GetAllStudentModuleReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_STUDENT_MODULE_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_STUDENT_MODULE_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_STUDENT_MODULE_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
