import React, { lazy } from 'react';
import { Route, Switch, useLocation } from 'react-router-dom';

// project imports
import GuestGuard from './../utils/route-guard/GuestGuard';
import MinimalLayout from './../layout/MinimalLayout';
import NavMotion from './../layout/NavMotion';
import Loadable from '../ui-component/Loadable';

// login routing
const AuthLogin = Loadable(lazy(() => import('../views/authentication/login')));
const AuthRegister = Loadable(lazy(() => import('../views/authentication/register')));
const AuthForgotPassword = Loadable(lazy(() => import('../views/authentication/forgot-password')));
const AuthResetPassword = Loadable(lazy(() => import('../views/authentication/reset-password')));
const AuthLoginWithOtp = Loadable(lazy(() => import('../views/authentication/login-otp')));
const AuthVerifyOtp = Loadable(lazy(() => import('../views/authentication/login-otp/VerifyOtp')));

//-----------------------|| AUTH ROUTING ||-----------------------//

const LoginRoutes = () => {
    const location = useLocation();

    return (
        <Route path={['/login', '/register', '/forgot-password', '/reset-password/:token', '/login-otp', '/verify-otp']}>
            <MinimalLayout>
                <Switch location={location} key={location.pathname}>
                    <NavMotion>
                        <GuestGuard>
                            <Route path="/login" component={AuthLogin} />
                            <Route path="/register" component={AuthRegister} />
                            <Route path="/forgot-password" component={AuthForgotPassword} />
                            <Route path="/reset-password/:token" component={AuthResetPassword} />
                            <Route path="/login-otp" component={AuthLoginWithOtp} />
                            <Route path="/verify-otp" component={AuthVerifyOtp} />
                        </GuestGuard>
                    </NavMotion>
                </Switch>
            </MinimalLayout>
        </Route>
    );
};

export default LoginRoutes;
