import { GET_FOCUS_SKILL_START, GET_FOCUS_SKILL_SUCCESS, GET_FOCUS_SKILL_ERROR } from '../../constants';

const initialState = {
    loading: false,
    allFocusSkills: null,
    message: ''
};

export default function GetAllFocusSkillsReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_FOCUS_SKILL_START:
            return {
                ...state,
                loading: true
            };
        case GET_FOCUS_SKILL_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_FOCUS_SKILL_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
