import { GET_ALL_DELIVERY_PLAN_START, GET_ALL_DELIVERY_PLAN_SUCCESS, GET_ALL_DELIVERY_PLAN_ERROR } from '../../constants';

const initialState = {
    loading: false,
    allDeliveryPlan: null,
    message: ''
};

export default function GetAllDeliveryPlanReducer(state = { ...initialState }, action) {
    switch (action.type) {
        case GET_ALL_DELIVERY_PLAN_START:
            return {
                ...state,
                loading: true
            };
        case GET_ALL_DELIVERY_PLAN_SUCCESS:
            return {
                ...state,
                ...action.payload,
                loading: false
            };
        case GET_ALL_DELIVERY_PLAN_ERROR:
            return {
                ...state,
                loading: false,
                message: action.error.message
            };
        default:
            return state;
    }
}
